* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

:root {
    --primaryColor: #af9a7d;
    --mainWhite: #fff;
    --offWhite: #f7f7f7;
    --mainBlack: #222;
    --mainGrey: #ececec;
    --darkGrey: #cfcfcf;
    --mainTransition: all 0.3s linear;
    --mainSpacing: 3px;
    --lightShadow: 2px 5px 3px 0px rgba(0, 0, 0, 0.5);
    --darkShadow: 4px 10px 5px 0px rgba(0, 0, 0, 0.5);
}

body {
    padding-top: 66px;
    color: var(--mainBlack);
    background: var(--mainWhite);
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    line-height: 1.4;
}

h1 {
    font-size: 3em;
    line-height: 1;
    margin-bottom: 0.5em;
}
h2 {
font-size: 2em;
margin-bottom: 0.75em;
}
h3 {
font-size: 1.5em;
line-height: 1;
margin-bottom: 1em;
}
h4 {
font-size: 1.2em;
line-height: 1.25;
margin-bottom: 1.25em;
}
h5 {
font-size: 1em;
font-weight: bold;
margin-bottom: 1.5em;
}
h6 {
font-size: 1em;
font-weight: bold;
margin-bottom: 1.5em;
}

.btn-primary {
display: inline-block;
text-decoration: none;
letter-spacing: var(--mainSpacing);
color: var(--mainBlack);
background: var(--primaryColor);
padding: 0.4rem 0.9rem;
border: 3px solid var(--primaryColor);
transition: var(--mainTransition);
text-transform: uppercase;
cursor: pointer;
}

/* Navbar */
.navbar {
position: fixed;
top: 0;
left: 0;
width: 100%;
padding: 0.75rem 2em;
background: var(--offWhite);
z-index: 1;
}

.nav-header {
display: flex;
justify-content: space-between;
}

.brand-name {
    width: 200px;
    border: 2px solid black;
    text-align: center;
    box-shadow: inset 0 -2px 10px rgba(0, 0, 0, 0.397);
}

.brand-name a {
    letter-spacing: var(--mainSpacing);
    margin: 0 auto;
    font: bold 20px system-ui;
    text-shadow: 1px 1px black;
    text-decoration: none;
    color: rgb(194, 126, 71);
}

.nav-btn {
background: transparent;
border: none;
cursor: pointer;
outline: none;
}

.nav-icon {
font-size: 1.5rem;
color: var(--primaryColor);
}

.nav-links {
height: 0;
overflow: hidden;
transition: var(--mainTransition);
list-style-type: none;
}

.nav-links a {
display: block;
text-decoration: none;
padding: 1rem 0;
color: var(--mainBlack);
transition: var(--mainTransition);
text-align: center;
font-size: 1rem;
font-weight: 600;
letter-spacing: var(--mainSpacing);
}

.nav-links a:hover {
color: var(--primaryColor)
}

.show-nav {
height: 100px;
}

@media (min-width: 768px) {
.nav-btn {
    display: none;
}
.nav-center {
    max-width: 1170px;
    margin: 0 auto;
    display: flex;
}
.nav-links {
    height: auto;
    display: flex;
    margin-left: 4rem;
}
.nav-links a {
    margin: 0 1rem;
    padding: 0.5rem 0;
}
}

/* Hero */

.defaultHero, 
.roomsHero {
min-height: calc(100vh - 66px);
background: url("./images/defaultBcg.jpeg") center/cover no-repeat;
display: flex;
align-items: center;
justify-content: center;
}

.roomsHero {
background-image: url('./images/room-2.jpeg');
min-height: 60vh;
}

/* Banner */

.banner {
display: inline-block;
background: rgba(0, 0, 0, 0.5);
color: var(--mainWhite);
padding: 2rem 1rem;
text-align: center;
text-transform: capitalize;
letter-spacing: var(--mainSpacing);
}

.banner h1 {
font-size: 2.5rem;
}

.banner div {
width: 10rem;
height: 5px;
background: var(--primaryColor);
margin: 1.7rem auto;
}

.banner p {
font-size: 1.2rem;
margin-bottom: 2rem;
}

@media screen and (min-width: 576px) {
.banner {
    padding: 2rem 3rem;
}
.banner h1 {
    font-size: 3rem;
}
}
@media screen and (min-width: 992px) {
.banner {
    padding: 2rem 6rem;
}
.banner h1 {
    font-size: 4rem;
}
}

/* Services */

.services {
    padding: 5rem 0;
}

.services {
    background: var(--darkGrey);
    text-align: center;
}

.services-center {
    width: 90vw;
    margin: 0 auto;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(255px, 1fr));
    grid-row-gap: 2rem;
    grid-column-gap: 50px;
}

.services span {
    display: inline-block;
    color: var(--primaryColor);
    font-size: 2.5em;
    margin-bottom: 1.5rem;
}

.services h6 {
    letter-spacing: var(--mainSpacing);
    text-transform: uppercase;
}

.services p {
    width: 80%;
    margin: 0 auto;
}

@media (min-width: 992px) {
    .services-center {
        width: 95vw;
        max-width: 1170px;
    }
}

@media (min-width: 1200px) {
    .services p {
        width: 100%;
    }
}

/* Title */
.section-title {
    text-align: center;
    margin-bottom: 4rem;
}

.section-title h4 {
    font-size: 2rem;
    letter-spacing: var(--mainSpacing);
    text-transform: capitalize;
    margin-bottom: 1rem;
}

.section-title div {
    width: 5rem;
    height: 5px;
    margin: 0 auto;
    background: var(--primaryColor);
}

/* room */

.room {
    box-shadow: var(--lightShadow);
    transition: var(--mainTransition);
}

.room:hover {
    box-shadow: var(--darkShadow);
}

.img-container {
    position: relative;
}

.img-container img {
    width: 100%;
    display: block;
    transition: var(--mainTransition);
}

.price-top {
position: absolute;
top: 0;
left: 0;
background: rgba(0, 0, 0, 0.8);
color: var(--mainWhite);
padding: 0.3rem 0.6rem 0.5rem;
border-bottom-right-radius: 1rem;
font-size: 0.5rem;
text-align: center;
transition: var(--mainTransition);
}

.price-top h6 {
    margin-bottom: 0;
    font-size: 0.9rem;
    font-weight: 300;
    letter-spacing: var(--mainSpacing);
}

.room-link {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: scale(0);
    transition: all 0.3s linear;
}

.img-container:hover {
    background: rgba(0, 0, 0, 0.8);
}

.img-container:hover img {
    opacity: 0.3;
}

.img-container:hover .price-top {
    opacity: 0;
}

.img-container:hover .room-link {
    transform: translate(-50%, -50%) scale(1);
}

.room-info {
    background: var(--darkGrey);
    text-transform: capitalize;
    padding: 0.5rem 0;
    text-align: center;
    font-weight: 700;
    letter-spacing: var(--mainSpacing);
}

/* featured rooms */

.featured-rooms {
    padding: 5rem 0;
}

.featured-rooms-center {
    width: 80vw;
    margin: 0 auto;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(270px, 1fr));
    grid-row-gap: 2rem;
    grid-column-gap: 40px;
}

@media screen and (min-width: 776px) {
.featured-rooms-center {
    width: 90vw;
    grid-template-columns: repeat(auto-fit, minmax(330px, 1fr));
}
}
@media screen and (min-width: 992px) {
.featured-rooms-center {
    width: 95vw;
    max-width: 1170px;
}
}

/* roomslist */

.roomslist {
padding: 5rem 0;
}

.roomslist-center {
width: 80vw;
margin: 0 auto;
display: grid;
grid-template-columns: repeat(auto-fill, minmax(270px, 1fr));
grid-row-gap: 2rem;
grid-column-gap: 30px;
}

@media screen and (min-width: 776px) {
.roomslist-center {
    width: 90vw;
}
}
@media screen and (min-width: 992px) {
.roomslist-center {
    width: 95vw;
    max-width: 1170px;
}
}

/* filter-form */ 
.filter-container {
    padding: 3rem;
}

.filter-form {
    width: 60vw;
    margin: 0 auto;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(202px, 1fr));
    row-gap: 2rem;
    column-gap: 40px;
}

.form-group {
    text-transform: capitalize;
}

.form-group label {
    display: block;
    margin-bottom: 0.5rem;
    letter-spacing: var(--mainSpacing);
}

.form-control {
    width: 100%;
    background: transparent;
    font-size: 1rem;
}

.single-extra label {
display: inline-block;
font-size: 0.8rem;
margin: 0 1rem 0 0.5rem;
}

.size-input {
width: 40%;
padding: 0.2rem;
border: 1px solid var(--mainBlack);
border-radius: 0.3rem;
margin-right: 0.3rem;
}

@media screen and (min-width: 776px) {
.filter-form {
    width: 70vw;
}
}
@media screen and (min-width: 992px) {
.filter-form {
    width: 95vw;
    max-width: 1170px;
}
}

/* single-room */

.single-room {
    padding: 5rem 0 0 0;
}

.single-room-images {
    width: 80vw;
    margin: 0 auto;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
    column-gap: 50px;
    row-gap: 2rem;
}

.single-room-images img {
    width: 100%;
    display: block;
}

.single-room-info {
    width: 80vw;
    display: grid;
    grid-template-columns: 1fr;
    margin: 2rem auto;
}

.desc,
.info {
    margin: 1rem 0;
}

.desc h3 {
text-transform: capitalize;
letter-spacing: var(--mainSpacing);
}

.desc p {
    line-height: 1.5;
}

.info h3,
.info h6 {
    text-transform: capitalize;
    letter-spacing: var(--mainSpacing);
}

.info h6 {
    font-weight: 300;
}

.room-extras {
width: 80vw;
margin: 0 auto 3rem auto;
}

.room-extras h6 {
text-transform: capitalize;
letter-spacing: var(--mainSpacing);
}

.extras {
list-style-type: none;
display: grid;
grid-template-columns: repeat(auto-fit, minmax(330px, 1fr));
grid-column-gap: 2rem;
grid-row-gap: 1rem;
}

@media screen and (min-width: 992px) {
.single-room-images,
.single-room-info,
.room-extras {
    width: 95vw;
    max-width: 1170px;
}
.single-room-info {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 2rem;
}
.info {
    padding-left: 3rem;
}
}
